@use "../helpers/mixins" as *;
@use "../variables/colors" as *;
@use "../variables/dimensions" as *;
.section-head {
  margin-block: 50px;
  padding: 10px;
  text-align: center;
  background-color: $blue-3-color;
  text-transform: capitalize;
  font-size: 50px;
  position: relative;
  &::before {
    rotate: 180deg;
    bottom: 100%;
  }
  &::after {
    top: 100%;
  }
  &::after,
  &::before {
    content: "";
    width: 100%;
    height: 7px;
    position: absolute;
    background-size: 26px 30px;
    background-repeat: repeat;
    background-image: linear-gradient(
        141deg,
        $blue-3-color 20%,
        transparent 20%
      ),
      linear-gradient(215deg, $blue-3-color 20%, transparent 20%);
    left: 0;
  }
}
